<template>
  <div>
    <el-table
        :data="tableData"
        stripe
        style="width: 100%">
      <el-table-column
          v-for="item in tableLabel"
          :prop="item.prop"
          :key="item.prop"
          :label="item.label"
          :width="item.width?item.width:180">
        <template slot-scope="scope">
          {{scope.row[item.prop]}}
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button @click="handleEdit(scope.row)">编辑</el-button>
          <el-button @click="handleDelete(scope.row)" type="danger">删除</el-button>
        </template>

      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="prev, pager, next"
        :page-size="config.pageSize"
        :current-page.sync="config.page"
        @current-change="changePage"
        :total="config.total">
    </el-pagination>
  </div>
</template>

<script>

export default {
  name: "CommonTable",
  props:{
    tableData:Array,
    tableLabel:Array,
    config:Object
  },
  methods:{
    handleEdit(item){
      this.$emit('edit',item);
    },
    handleDelete(item){
      this.$emit('delete',item)
    },
    changePage(){
      this.$emit('get')
    }
  }
}
</script>

