<template>
  <div>
    <div style="display: flex;justify-content: space-between">
      <el-button type="primary" @click="isShow=true">+新增</el-button>
      <el-dialog :visible.sync='isShow' title="新增用户">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="姓名">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="年龄">
            <el-input-number v-model="form.age" :min="1" :max="100" label="年龄"></el-input-number>
          </el-form-item>
          <el-form-item label="性别">
            <el-radio v-model="form.sex" label="男">男</el-radio>
            <el-radio v-model="form.sex" label="女">女</el-radio>
          </el-form-item>
          <el-form-item label="生日">
            <el-date-picker
                v-model="form.birth"
                type="date"
                value-format="yyyy-MM-dd"
                placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="地址">
            <el-input v-model="form.address"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="addUser">立即创建</el-button>
            <el-button @click="isShow=false">取消</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
      <div id="searchUser">
        <el-input type="input" size="small" style="width: 150px;margin-right: 20px;" v-model="keyword"></el-input>
        <el-button type="primary" @click="getUser(keyword)">搜索</el-button>
      </div>
    </div>
    <div id="userTable">
      <CommonTable @get="getUser" @edit="editUser" @delete="deleteUser" :tableData="tableData" :tableLabel="tableLabel" :config="config"></CommonTable>
    </div>
  </div>

</template>

<script>
import CommonTable from "@/components/CommonTable";
import axios from "axios";
export default {
  name: "User",
  components: {
    CommonTable,
  },
  methods: {
    addUser(){
      this.isShow = false
      let url = "http://127.0.0.1:8080/user/addUser";
      let data = JSON.stringify(this.form)
      axios({
        url,
        data:data,
        method: 'post',
        headers: {
          'Content-Type':'application/json'
        }
      }).then(res => {
        console.log(res)
      })
    },
    editUser(item){
      this.isShow=true;
      this.form=item;
    },
    deleteUser(item){
      this.tableData.splice(this.tableData.findIndex(it=>it.name===item.name),1)
    },
    getUser(keyword){
      axios.get('/user/getUsers',{
        params:{
          page:this.config.page,
          pageSize:this.config.pageSize,
          keyword:keyword,
        }
      }).then(
          response => {
            this.tableData=response.data.data.records;
            this.config.total=response.data.data.total;
          }
      )
    }
  },
  data() {
    return {
      isShow: false,
      keyword:'',
      form: {
        name: '',
        age: '',
        sex: '',
        birth: '',
        address: '',
      },
      tableData:[],
      tableLabel:[
        {prop:'name', label: '姓名'},
        {prop:'sex', label: '性别'},
        {prop:'age', label: '年龄'},
        {prop:'birth', label: '生日'},
        {prop:'address', label: '地址'},
      ],
      config:{
        page:1,
        pageSize:10,
        total:0,
      },
    }
  },
  watch:{
    keyword(newValue){
      console.log(newValue)
      if (newValue === '') {
        this.getUser(newValue);
      }
    }
  },
  mounted() {
    this.getUser();
  }
}
</script>

