import Vue from "vue";
import Vuex from "vuex"

Vue.use(Vuex)
const state = {
    isCollapsed: false,
    menuList:[
        {
            path:'/',
            name:'home',
            label:'主页',
            icon:'home',
        },
    ],
    currentMenu:null,
}

const mutations = {
    collapseMenu(state) {
        state.isCollapsed = !state.isCollapsed
    },
    selectMenu(state,val){
        if (val.name!=='home'){
            state.currentMenu=val;
            let index=state.menuList.findIndex(item => {
                return item.name===val.name
            })
            if (index === -1) {
                state.menuList.push(val)
            }
        }else{
            state.currentMenu=null;
        }
    }
}
export default new Vuex.Store({
    state,mutations
})