<template>
  <el-row class="home" :gutter="20">
    <el-col :span="8" style="margin-top: 20px">
      <el-card shadow="hover">
        <div class="user" style="display: flex">
          <img :src="userIcon">
          <div class="userInfo">
            <p class="name">Admin</p>
            <p class="access">超级管理员</p>
          </div>
        </div>
        <hr>
        <div class="loginInfo">
          <p>上次登录时间:<span>2022/7/18</span></p>
          <p>上次登录地点:<span>聊城</span></p>
        </div>
      </el-card>
      <el-card style="margin-top: 20px;height: 450px">
        <el-table :data="tableData" max-height="450">
          <el-table-column prop="phoneName" label="品牌"></el-table-column>
          <el-table-column prop="todayBuy" label="本日销量"></el-table-column>
          <el-table-column prop="monthBuy" label="本月销量"></el-table-column>
          <el-table-column prop="totalBuy" label="总销量"></el-table-column>
        </el-table>
      </el-card>
    </el-col>
    <el-col style="margin-top: 20px;" :span="16">
      <div class="num" style="display: flex;flex-wrap: wrap;justify-content:space-evenly;align-content:space-between;height: 170px">
        <el-card v-for="item in countData" :key="item.name" body-style="display: flex;padding:0px;width:200px;height:80px;">
          <i class="icon" :class="`el-icon-${item.icon}`" :style="{background:item.color}"></i>
          <div class="detail">
            <p class="num">${{item.value}}</p>
            <p class="txt">{{item.name}}</p>
          </div>
        </el-card>
      </div>
      <el-card style="height: 260px;margin-top: 20px">
        <div style="height: 260px;width: 793px" id="sales">
        </div>
      </el-card>
      <div class="graph" style="display: flex">
        <el-card style="height: 205px;margin-top: 20px;width: 390px;">
          <div id="userEcharts" style="height:205px;width: 390px"></div>
        </el-card>
        <el-card style="height: 205px;margin-top: 20px;width: 390px;margin-left: 20px">
          <div id="videoEcharts" style="height: 180px;width: 390px"></div>
        </el-card>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import * as echarts from 'echarts';

export default {
  name: "Home",
  data() {
    return {
      userIcon: require('../assets/仙人掌.png'),
      tableData: [],
      userData:[],
      videoData:[],
      countData: [
        {
          name: '今日支付订单',
          value: 1234,
          icon: 'success',
          color: '#2ec7c9',
        },
        {
          name: '今日收藏订单',
          value: 210,
          icon: 'star-on',
          color: '#ffb980',
        }, {
          name: '今日未支付订单',
          value: 1234,
          icon: 's-goods',
          color: '#5ab1ef',
        }, {
          name: '本月支付订单',
          value: 1234,
          icon: 'success',
          color: '#2ec7c9',
        }, {
          name: '本月收藏订单',
          value: 210,
          icon: 'star-on',
          color: '#ffb980',
        },
        {
          name: '本月未支付订单',
          value: 1234,
          icon: 's-goods',
          color: '#5ab1ef',
        },
      ]
    }
  },
  mounted() {
    axios.get('/home/getPhoneData')
        .then(response =>{
          console.log(response)
          this.tableData=response.data.data;
          let phoneName=[];
          let todayBuy=[];
          let monthBuy=[];
          let totalBuy=[];
          this.tableData.forEach(item=>{
            //phoneName.push(item.phoneName);//forEach写法
            todayBuy.push(item.todayBuy);
            monthBuy.push(item.monthBuy);
            totalBuy.push(item.totalBuy);
          })

          // 基于准备好的dom，初始化echarts实例
          let myChart = echarts.init(document.getElementById('sales'));
          // 绘制图表
          let option = {
            xAxis: {
              type: 'category',
              data: this.tableData.map(item => item.phoneName)//map写法
            },
            yAxis: {},
            legend:{
              orient: 'horizontal',
              right: 260,
              top: 20,
            },
            series: [
              {
                type: 'line',
                name: 'todayBuy',
                data: todayBuy,
              },
              {
                type: 'line',
                name: 'monthBuy',
                data: monthBuy,
              },
              {
                type: 'line',
                name: 'totalBuy',
                data: totalBuy,
              }
            ]
          };
          myChart.setOption(option);
        })
        .catch(function (error) {
          // 处理错误情况
        })
        .then(function () {
          // 总是会执行
        });
    axios.get('/home/getUserData').then(
        response =>{
          this.userData=response.data.data;
          let userChart = echarts.init(document.getElementById('userEcharts'));
          console.log(this.userData)
          // 绘制图表
          let option = {
            xAxis: {
              type: 'category',
              data: this.userData.map(item => item.week)//map写法
            },
            yAxis: {},
            legend:{
              orient: 'horizontal',
              right: 260,
              top: 0,
            },
            series: [
              {
                type: 'bar',
                name: '新增',
                data: this.userData.map(item => item.newUser),
              },
              {
                type: 'bar',
                name: '活跃',
                data: this.userData.map(item => item.activeUser),
              },
            ]
          };
          userChart.setOption(option);
        }
    )
    axios.get('/home/getVideoData').then(
        response => {
          this.videoData=response.data.data;
          let videoChart = echarts.init(document.getElementById('videoEcharts'));
          let option = {
            series: [
              {
                type: 'pie',
                data: this.videoData.map(item => {
                  return {name:item.videoName,value:item.count}
                }),
              }
            ]
          };
          videoChart.setOption(option);
        },
    )
  }
}
</script>

<style scoped>
.icon{
  display: inline-block;
  width: 70px;
}
</style>