<template>
  <div id="tags">
    <el-tag
        v-for="tag in tags"
        :key="tag.name"
        closable
        @close="handleClose(tag)"
        @click="handleClick(tag)"
        :effect="$route.name===tag.name?'dark':'light'"
        style="margin-left: 10px;cursor: pointer"
        :type="tag.type">
      {{ tag.name }}
    </el-tag>
  </div>

</template>

<script>
import {mapState} from "vuex";

export default {
  name: 'CommonTags',
  data() {
    return {
    };
  },
  methods:{
    handleClose(tag){
      this.$store.state.menuList.splice(this.$store.state.menuList.indexOf(tag),1)
    },
    handleClick(tag){
      this.$router.push({
        name:tag.name
      })
    }
  },
  computed: {
    ...mapState({tags:state => state.menuList})
  }
}
</script>

